import axios from '@/libs/api.request'
import { stringFormat, containingChinese } from '@/libs/tools'
import originalAxios from 'axios'

export const getTableLabel = () => {
  return axios.request({
    url: 'transactionColumns',
    method: 'get'
  })
}

export const getTableData = (pageIndex, pageSize, orderName, direction, searchText) => {
  let param = searchText
    ? '/' + (containingChinese(searchText) ? encodeURI(searchText) : searchText)
    : ''
  let url = stringFormat('globalTransactions/{0}/{1}/{2}/{3}{4}', [
    pageIndex,
    pageSize,
    orderName,
    direction === 'descending' ? 'desc' : 'asc',
    param
  ])
  return axios.request({
    url,
    method: 'get'
  })
}

export const getTable = (pageIndex, pageSize, orderName, direction, searchText) => {
  return originalAxios.all([
    getTableLabel(),
    getTableData(pageIndex, pageSize, orderName, direction, searchText)
  ])
}

export const postSubTransactions = globalTxIds => {
  const data = {
    globalTxIds
  }
  return axios.request({
    url: 'subTransactions',
    data,
    method: 'post'
  })
}

