<template>
  <div>
    <Row>
      <Col span="20">
        <Button @click="freshen" type="primary" class="margin-right10">刷新当前页</Button>
        <Button @click="showSubtransaction" type="primary" class="margin-right10">显示子事务</Button>
        <Button @click="hide" type="primary" class="margin-right10">隐藏子事务</Button>
        <Button @click="pauseGts" type="primary" class="margin-right10">暂停全局事务</Button>
        <Button @click="continueGts" type="primary" class="margin-right10">继续全局事务</Button>
        <Button @click="stopGts" type="primary" class="margin-right10">终止全局事务</Button>
        <Poptip
          confirm
          title="降级全局事务后，所有正在运行(含新业务)都将在无全局事务下运行，请确认？"
          @on-ok="degradeGts"
          @on-cancel="cancel"
        >
          <Button type="primary" class="margin-right10">降级全局事务</Button>
        </Poptip>
        <Button @click="startGts" type="primary" class="margin-right10">启用全局事务</Button>
      </Col>
      <Col span="4">
        <Input v-model="input" style="width:200px;float:right;" placeholder="请输入内容">
          <Button slot="append" icon="ios-search" @click="search()"></Button>
        </Input>
      </Col>
    </Row>
    <el-table
      ref="dear"
      empty-text="暂无数据"
      @selection-change="selectionChange"
      @sort-change="sortChange"
      :data="tableData"
      style="width: 100%; margin-top:10px; margin-bottom: 20px;"
      row-key="surrogateid"
      border
      stripe
      :default-sort="{prop: orderName, order: direction}"
      :row-class-name="rowClassName"
    >
      <el-table-column type="selection" width="60" align="center" :selectable="selectable"></el-table-column>
      <el-table-column
        v-for="(item, index) in tableLabel"
        :key="index"
        :prop="item.prop"
        :label="item.label"
        :sortable="excludeSortFieldName.indexOf(item.label) === -1 ? 'custom' : false"
        :width="item.label !== '状态' ? 0 : 80"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span v-if="scope.column.property != 'status'">{{scope.row[scope.column.property]}}</span>
          <span v-if="scope.column.property == 'status'">
            <i
              class="el-icon-video-play"
              style="font-size:22px;color:#00A8E8;"
              title="运行中"
              v-if="scope.row.status=='运行中'"
            ></i>
            <i
              class="el-icon-video-pause"
              style="font-size:22px;color:#285096;"
              title="暂停"
              v-if="scope.row.status=='暂停'"
            ></i>
            <i
              class="el-icon-warning-outline"
              style="font-size:22px;color:#146ACE;"
              title="运行异常"
              v-if="scope.row.status=='运行异常'"
            ></i>
            <i
              class="el-icon-timer"
              style="font-size:22px;color:#1498EE;"
              title="正常结束"
              v-if="scope.row.status=='正常结束'"
            ></i>
            <i
              class="el-icon-help"
              style="font-size:22px;color:#44BCFF;"
              title="异常结束"
              v-if="scope.row.status=='异常结束'"
            ></i>
          </span>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 10px; overflow: hidden">
      <div style="float: right;">
        <Page
          v-if="total>pageSize"
          :total="total"
          :current="pageNo"
          @on-change="changePage"
          :page-size="pageSize"
          show-total
          show-elevator
        ></Page>
      </div>
    </div>
    <div style="display:none">
      <Modal v-model="modalContinueGts" title="Txle" @on-ok="continueAllGts" @on-cancel="cancel">
        <p>您未选中任何记录，将对所有记录进行操作，请确认？</p>
      </Modal>
      <Modal v-model="modalPauseGts" title="Txle" @on-ok="pauseAllGts" @on-cancel="cancel">
        <p>您未选中任何记录，将对所有记录(含新业务)进行暂停操作，请确认？</p>
      </Modal>
      <Modal v-model="modalSubTs" title="Txle" @on-ok="showAllSubtransaction" @on-cancel="cancel">
        <p>您未选中任何记录，将对本页所有全局事务进行操作，请确认？</p>
      </Modal>
    </div>
  </div>
</template>
<script>
import { distinct } from '@/libs/tools'
import {
	getTable,
	getTableData,
	getStartGlobalTransaction,
	getDegradeGlobalTransaction,
	getRecoverAllGlobalTransactions,
	getPauseAllGlobalTransactions,
	postTerminateGlobalTransactions,
	postRecoverGlobalTransactions,
	postPauseGlobalTransactions,
	postSubTransactions,
} from '@/api/globalTransactions'
import '../style/global.less'
export default {
	data() {
		return {
			tableLabel: [],
			tableData: [],
			pageSize: 10,
			pageNo: 1,
			total: 0,
			input: '',
			orderName: 'creationtime',
			direction: 'descending',
			excludeSortFieldName: ['状态', '结束时间', '全局事务标识'],
			selection: [],
			modalContinueGts: false,
			modalPauseGts: false,
			modalSubTs: false,
		}
	},
	mounted() {
		this.init()
	},
	methods: {
		selectionChange(rows) {
			this.selection = rows
		},
		sortChange(scope) {
			this.orderName = scope.prop
			this.direction = scope.order
			this.bindData()
		},
		search() {
			if (!this.input) {
				return this.$Message.info('请输入内容')
			}
			this.pageNo = 1
			this.bindData()
		},
		bindData(callback) {
			let params = [
				this.pageNo,
				this.pageSize,
				this.orderName,
				this.direction,
				this.input,
				callback,
			]
			this.getData(...params)
		},
		getData(pageNo, pageSize, orderName, direction, keyword, callback) {
			getTableData(...arguments).then(res => {
				this.tableData.splice(0, this.pageSize)
				res.recordSet.forEach(column => {
					var item = { hasChildren: true }
					this.tableLabel.forEach(label => {
						item[label.prop] = column[label.prop]
					})
					this.tableData.push(item)
				})
				this.total = res.recordsTotal

				if (typeof callback === 'function') {
					callback()
				}
			})
		},
		changePage(e) {
			this.pageNo = e
			this.bindData()
		},
		init() {
			let params = [this.pageNo, this.pageSize, this.orderName, this.direction, this.input]
			getTable(...params).then(res => {
				res[0].recordSet.forEach(item => {
					if (item.display) {
						this.tableLabel.push({
							prop: item.field,
							label: item.fieldname,
						})
					}
				})
				res[1].recordSet.forEach(column => {
					var item = { hasChildren: true }
					this.tableLabel.forEach(label => {
						item[label.prop] = column[label.prop]
					})
					this.tableData.push(item)
					this.total = res[1].recordsTotal
				})
			})
		},
		showAllSubtransaction() {
			let array = this.tableData.map(element => {
				return element.globaltxid
			})
			postSubTransactions(distinct(array).join(',')).then(res => {
				this.$Message.info('显示子事务成功！')
				this.pushSubtransaction(res.recordSet)
			})
		},
		pushSubtransaction(newArr) {
			let newTable = []
			this.tableData.forEach(c => {
				newTable.push(c)
				let temp = newArr.filter(d => {
					return d.globaltxid === c.globaltxid
				})
				newTable = newTable.concat(temp)
			})
			this.tableData = newTable
		},
		showSubtransaction() {
			if (this.selection.length > 0) {
				let array = []
				this.selection.forEach(e => {
					array.push(e.globaltxid)
				})
				if (array.length > 0) {
					postSubTransactions(array.join(',')).then(res => {
						if (res.recordSet.length > 0) {
							this.$Message.info('显示子事务成功！')
							this.pushSubtransaction(res.recordSet)
						} else {
							this.$Message.info('抱歉，这行事务没有子事务')
						}
					})
				} else {
					this.$Message.info('显示子事务中止')
				}
			} else {
				this.modalSubTs = true
			}
		},
		hide() {
			let result = []
			let obj = {}
			if (this.selection.length > 0) {
				let temp = this.selection.map(m => {
					return m.globaltxid
				})
				for (let i of this.tableData) {
					if (temp.indexOf(i.globaltxid) === -1) {
						result.push(i)
					} else if (!obj[i.globaltxid]) {
						result.push(i)
						obj[i.globaltxid] = 1
					}
				}
			} else {
				for (let i of this.tableData) {
					if (!obj[i.globaltxid]) {
						result.push(i)
						obj[i.globaltxid] = 1
					}
				}
			}
			this.tableData = result
		},
		pauseAllGts() {
			getPauseAllGlobalTransactions().then(res => {
				this.$Message.info('暂停全局事务成功！')
				this.bindData()
			})
		},
		pauseGts() {
			if (this.selection.length > 0) {
				let array = []
				this.selection.forEach(e => {
					let state = parseInt(e.status_db)
					if (state === 0) array.push(e.globaltxid)
				})
				if (array.length > 0) {
					postPauseGlobalTransactions(array.join(',')).then(res => {
						this.$Message.info('暂停全局事务成功！')
						this.bindData()
					})
				} else {
					this.$Message.info('暂停全局事务中止')
				}
			} else {
				this.modalPauseGts = true
			}
		},
		continueAllGts() {
			getRecoverAllGlobalTransactions().then(res => {
				this.$Message.info('继续全局事务成功！')
				this.bindData()
			})
		},
		continueGts() {
			if (this.selection.length > 0) {
				let array = []
				this.selection.forEach(e => {
					let state = parseInt(e.status_db)
					if (state === 2) array.push(e.globaltxid)
				})
				if (array.length > 0) {
					postRecoverGlobalTransactions(array.join(',')).then(res => {
						this.$Message.info('继续全局事务成功！')
						this.bindData()
					})
				} else {
					this.$Message.info('继续全局事务中止')
				}
			} else {
				this.modalContinueGts = true
			}
		},
		stopGts() {
			if (this.selection.length > 0) {
				let array = []
				this.selection.forEach(e => {
					let state = parseInt(e.status_db)
					if (state < 3) array.push(e.globaltxid)
				})
				if (array.length > 0) {
					postTerminateGlobalTransactions(array.join(',')).then(res => {
						this.$Message.info('终止全局事务成功！')
						this.bindData()
					})
				} else {
					this.$Message.info('终止全局事务中止')
				}
			} else {
				this.$Message.info('您未选中任何记录')
			}
		},
		degradeGts() {
			getDegradeGlobalTransaction().then(res => {
				this.$Message.info('降级全局事务成功！')
				this.bindData()
			})
		},
		startGts() {
			getStartGlobalTransaction().then(res => {
				this.$Message.info('启用全局事务成功！')
				this.bindData()
			})
		},
		cancel() {
			this.$Message.info('已取消操作')
		},
		selectable(row, index) {
			if ('hasChildren' in row) {
				return true
			}
			return false
		},
		rowClassName(obj) {
			if (obj.row.hasChildren === true) {
				return 'info-row'
			}
			return 'sub-info-row'
		},
		freshen() {
			this.input = ''
			this.bindData()
		},
	},
}
</script>
<style>
.sub-info-row td {
	background-color: #8baadd !important;
	color: #fff;
}
.sub-info-row .el-checkbox__input {
	display: none;
}
</style>
